@import "https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap";
:root {
  --header: 5rem;
  --footer: .2rem;
  --content: calc(100vh - (var(--header)  + var(--footer)));
}

* {
  margin: 0;
  font-family: Work Sans, serif;
}

body {
  color: #fff;
  background-color: #000;
  flex-direction: column;
  min-height: 100vh;
  display: flex;
  position: relative;
}

h2 {
  margin-bottom: 18px;
}

p {
  margin-bottom: 15px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

ul li a {
  padding: 0;
}

a {
  color: #fff;
  text-decoration: none;
}

a:hover {
  color: #ff4191;
}

nav {
  height: var(--header);
  z-index: 99;
  flex-direction: column;
  width: 100%;
  display: flex;
  position: fixed;
  top: 0;
}

nav ul {
  padding: 0;
  display: flex;
}

nav ul li {
  color: #ff4191;
  padding: 20px;
  display: inline-block;
  position: relative;
}

nav ul li a {
  padding: 10px;
}

nav ul li ul {
  margin-left: 0;
  display: none;
}

nav ul li:hover {
  cursor: pointer;
}

nav ul li:hover ul {
  background-color: #e90074;
  display: block;
  position: absolute;
  left: 0;
}

nav ul li:hover ul li {
  margin-bottom: 5px;
  display: block;
}

nav ul li:hover ul li a {
  color: #fff;
  padding: 5px;
}

nav ul li:hover ul li:hover {
  background-color: #ff4191;
}

h2 {
  font-size: 50px;
}

